<template>
  	<div class="card">
		<h2>new plekollection</h2>
		<div
			class="list"
			v-for="(f, i) in this.$store.state.featureCollectionToCreate.features"
		>
			{{ f.properties.name }}
			<button
				
				@click="removeFeature(f)"
				class="btn3"
			>
				-
			</button>
		</div>
        <button class="btn btn4" @click="save()">
            save
        </button>
	</div>
</template>

<script>
var api = require("@/utils/api");
export default { 
methods:{
        removeFeature(feature){
            if (
				confirm(
					`Delete "${feature.properties.name}" from new plekollection?`
				)
			) {
				this.$store.state.featureCollectionToCreate.features =
					this.$store.state.featureCollectionToCreate.features.filter(
						(f) => {return f.properties.name != feature.properties.name}
					);
			} else {
				console.log("canceled");
			}
        },
        save(){
            api.saveCreatedPlekollection(()=>{});
        }
    }
}
</script>

